@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
:root {
    --primarycolor: #fff;
    --dark: #212121;
    --accent: #ded008;
    --borderadius: 20px;
    --transition: all .4s ease-out;
}

.btn {
    padding: 1em 2em;
    border-radius: 20px;
    border-radius: var(--borderadius);
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: #fff;
    background-color: var(--primarycolor);
    color: #212121;
    color: var(--dark);
    border: 1px solid #fff;
    border: 1px solid var(--primarycolor);
}

.btn--secondary {
    background-color: transparent;
    color: #212121;
    color: var(--dark);
    padding: 8px 20px;
    border: 1px solid #212121;
    border: 1px solid var(--dark);
    transition: all .4s ease-out;
    transition: var(--transition);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    color: var(--primarycolor);
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primarycolor);
    transition: all .4s ease-out;
    transition: var(--transition);
}

.btn--secondary:hover {
    background: #212121;
    background: var(--dark);
    color: #fff;
    color: var(--primarycolor);
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover,
.btn--large:hover {
    background: #fff;
    background: var(--primarycolor);
    color: #212121;
    color: var(--dark);
    transition: all .4s ease-out;
    transition: var(--transition);
}
