a {
  text-decoration: none;
}

.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: center;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #fff;
    color: var(--primarycolor);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    justify-content: center;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #ded008;
    border-bottom: 4px solid var(--accent);
    transition: all .4s ease-out;
    transition: var(--transition);
  }
  
  .fa-bars {
    color: #fff;
    color: var(--primarycolor);
    transition: all .4s ease-out;
    transition: var(--transition);
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 768px) {
    .logo {
        align-items: center;
    }

    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: calc(100vh - 80px);
      position: absolute;
      left: -100%;
      opacity: 1;
      top: 79px;
      transition: all .4s ease-out;
      transition: var(--transition);
      padding-top: 0;
    }
  
    .nav-menu.active {
      background: #242222;
      padding: 0;
      left: 0;
      opacity: 1;
      transition: all .4s ease-out;
      transition: var(--transition);
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 0 2rem;
      width: 100%;
      top: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .nav-links:hover {
      background-color: #fff;
      background-color: var(--primarycolor);
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      -webkit-transform: translate(25%, 50%);
              transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #ded008;
      color: var(--accent);
      font-size: 2rem;
      transition: all .4s ease-out;
      transition: var(--transition);
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 20px;
      border-radius: var(--borderadius);
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      color: var(--primarycolor);
      padding: 14px 20px;
      border: 1px solid #fff;
      border: 1px solid var(--primarycolor);
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      background: var(--primarycolor);
      color: #242424;
      transition: all .4s ease-out;
      transition: var(--transition);
    }
  }
