* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
:root {
    --primarycolor: #fff;
    --greish: #c6c6c6;
    --dark: #212121;
    --accent: #ded008;
    --borderadius: 20px;
    --transition: all .4s ease-out;
    --shadow: 2px 2px 30px rgba(0,0,0,.2);
}

video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container {
    height: calc(100vh - 80px);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 2px 30px rgba(0,0,0,.2);
    box-shadow: var(--shadow);
    object-fit: contain;
}

.hero-container h1 {
    color: #fff;
    color: var(--primarycolor);
    letter-spacing: 3px;
    text-transform: uppercase;
    margin-bottom: 1em;
    text-align: center;
}

.hero-container p {
    color: #c6c6c6;
    color: var(--greish);
    text-align: center;
    margin-bottom: 4em;
}
:root {
    --primarycolor: #fff;
    --dark: #212121;
    --accent: #ded008;
    --borderadius: 20px;
    --transition: all .4s ease-out;
    --shadow: 2px 2px 30px rgba(0,0,0,.2);
}

.videos-container {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    background: #fff;
    background: var(--primarycolor);
    width: 100%;
    padding-top: 4em;
    padding-bottom: 4em;
    text-align: center;
}

.video-wrapper h1 {
    font-size: 1.8rem;
    font-weight: normal;
    letter-spacing: 1.4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: .3em;
}

.video-wrapper span::before {
    content: '';
    position: absolute;
    width: 60px;
    height: 4px;
    background: #212121;
    background: var(--dark);
    text-align: center;
    align-items: center;
}

.video-list {
    margin-top: 4em;
    padding: 0;
}

.lists {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.lists li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2em;
    list-style: none;
}

button {
    padding-bottom: 4em;
}

.videoThumb {
    border-radius: 20px;
    border-radius: var(--borderadius);
}

@media screen and (min-width: 700px) {
    .lists {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 20px;
        margin-bottom: 3em;
    }

    .lists li {
        margin-bottom: 0;
    }
}

@media screen and (min-width: 1000px) {
    .lists {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 17px;
        margin-bottom: 3em;
    }
}
:root {
    --primarycolor: #fff;
    --grey: #f5f5f5;
    --dark: #212121;
    --accent: #ded008;
    --borderadius: 20px;
    --transition: all .4s ease-out;
    --shadow: 2px 2px 30px rgba(0,0,0,.2);
}

.about-container {
    background: #f5f5f5;
    background: var(--grey);
    padding: 3em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.about-wrapper {
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

img {
    width: 150px;
    box-shadow: 2px 2px 30px rgba(0,0,0,.2);
    box-shadow: var(--shadow);
}

.about-details {
    padding: 0 1em;
    text-align: center;
    margin-bottom: 2em;
}

.about-details h1 {
    font-size: 1.8rem;
    font-weight: normal;
    letter-spacing: 1.4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: .3em;
}

.about-details p {
    margin-top: 2em;
}

.about-details span::before {
    content: '';
    position: absolute;
    width: 60px;
    height: 4px;
    background: #212121;
    background: var(--dark);
    left: 7.3em;
}

.about-images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
}

.about-images img:nth-child(1) {
    position: relative;
    left: 1.3em;
    top: 1em;
}
.about-images img:nth-child(2) {
    position: relative;
    right: -1.4em;
    bottom: -1.4em;
}
.about-images img:nth-child(3) {
    position: relative;
    width: 200px;
    right: -4em;
}
.about-images img:nth-child(4) {
    position: relative;
    width: 100px;
    right: -1em;
    top: 5em;

}
.about-images img:nth-child(5) {
    position: relative;
    top: -1em;
    left: 1em;
}

@media screen and (max-width: 425px) {
    .about-images img:nth-child(2) {
        right: .5em;
    }
}


@media screen and (min-width: 750px) {
    .about-wrapper {
        flex-direction: row;
        justify-content: space-between;
    }
    
    .about-details {
        text-align: left;
    }

    .about-details span::before {
        left: 4em;
    }
}

@media screen and (min-width: 1300px) {
    .about-details span::before {
        left: 8.5em;
    }
}
.footer-container {
